import { Link } from "gatsby";
import React from "react";
import Slider from "react-slick";
import productCateg1 from "../../assets/images/product/productCateg1.png";
import productCateg2 from "../../assets/images/product/productCateg2.png";
import productCateg3 from "../../assets/images/product/productCateg3.png";
import productCateg4 from "../../assets/images/product/productCateg4.png";
import productCateg5 from "../../assets/images/product/productCateg5.png";

const ProductDetails = () => {
  const settings = {
    dots: true,
    cssEase: "linear",
    dotsClass: "slick-dots slick-thumb",
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 1025,
        settings: {
          autoplay: true,
        },
      },
    ],
    appendDots: (dots) => <ul>{dots}</ul>,
    customPaging: (i) => (
      <div className="slick_dots_custom">
        <img src={productCateg1} alt="slides" />
      </div>
    ),
  };
  return (
    <div className="productdetail-section home-content">
      <div className="header-title">
        <div className="container">
          <div className="header-title-text">
            <h2>
              EXPLORE <span>OUR TILE RANGE</span>
            </h2>
            <ul>
              <li>
                <Link to="">Home</Link>
              </li>
              <li>/ Products</li>
              <li>/ Architectural Concrete</li>
            </ul>
          </div>
        </div>
      </div>

      <div className="productdetail-wrapper">
        <div className="productdetail-left">
          <div className="productdetail-slider">
            <Slider {...settings}>
              <div className="product-tiles">
                <div className="product-box">
                  <img src={productCateg1} alt="product-pic" />
                </div>
              </div>
              <div className="product-tiles">
                <div className="product-box">
                  <img src={productCateg2} alt="product-pic" />
                </div>
              </div>
              <div className="product-tiles">
                <div className="product-box">
                  <img src={productCateg3} alt="product-pic" />
                </div>
              </div>
              <div className="product-tiles">
                <div className="product-box">
                  <img src={productCateg4} alt="product-pic" />
                </div>
              </div>
              <div className="product-tiles">
                <div className="product-box">
                  <img src={productCateg5} alt="product-pic" />
                </div>
              </div>
            </Slider>
          </div>
        </div>

        <div className="productdetail-right">
          <div className="product-right-wrapper">
            <div className="productdetail-title">
              <h5>Architectural Concrete</h5>
              <span>$ 120.00</span>
            </div>
            <div className="productdetail-desc">
              <div className="productdeatails-subtitle">
                <h6>Series</h6>
                <span>:</span>
                <span>Cross Cut</span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Code</h6>
                <span>:</span>
                <span>60TC12- Matt 600x600mm</span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Description</h6>
                <span>:</span>
                <span>
                  Cross Cut Beige Matt P3 Italain version rect porcelain
                </span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Design Faces</h6>
                <span>:</span>
                <span>20</span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Slip Rating</h6>
                <span>:</span>
                <span>P3</span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Finish</h6>
                <span>:</span>
                <span>Matt</span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Edge</h6>
                <span>:</span>
                <span>Rectified</span>
              </div>
              <div className="productdeatails-subtitle">
                <h6>Area</h6>
                <span>:</span>
                <span>Floor / Wall</span>
              </div>
            </div>
            <div className="productdetail-color">
              <h6>Color</h6>
              <div className="color-catalog">
                <span>
                  <img
                    src="https://gotilesqld.com/wp-content/uploads/2020/11/front-pic-4.jpg"
                    alt="title catalog"
                  />
                </span>
                <span>
                  <img src={productCateg4} alt="title catalog" />
                </span>
                <span>
                  <img src={productCateg3} alt="title catalog" />
                </span>
              </div>
            </div>
            <Link to="/contactUs">Enquire Now</Link>
          </div>
        </div>
      </div>

      <div className="product-look-section home-contact">
        <div className="home-contact-grid">
          <h2>
            Have a <span> Look</span>
          </h2>
        </div>
      </div>
    </div>
  );
};

export default ProductDetails;
